h3 {
  font-weight: 100; }

p {
  text-indent: 1em; }

.no-baseline {
  lost-column: 1/2; }
  .no-baseline P {
    font-size: 1rem;
    line-height: 1.25;
    margin-bottom: 0.45em; }
  .no-baseline h1 {
    font-size: 4.213rem;
    line-height: 0.8;
    margin-bottom: 0.6em; }
  .no-baseline h2 {
    font-size: 3.16rem;
    line-height: 1.2; }
  .no-baseline h3 {
    font-size: 2.37rem;
    line-height: 0.9;
    margin-bottom: 1em; }
  .no-baseline h4 {
    font-size: 1.777rem;
    line-height: 1;
    margin-bottom: 0.5em; }
  .no-baseline h5 {
    font-size: 1.33rem;
    line-height: 1.5; }

.baseline {
  lost-column: 1/2; }
  .baseline p, .baseline ul, .baseline ol, .baseline pre, .baseline blockquote {
    font-size: 1rem;
    line-height: 1.25rem;
    margin-bottom: 1.25rem; }
  .baseline h1 {
    font-size: 4.213rem;
    line-height: 3.75rem;
    margin-bottom: 3.75rem; }
  .baseline h2 {
    font-size: 3.16rem;
    line-height: 2.5rem;
    margin-bottom: 3.75rem; }
  .baseline h3 {
    font-size: 2.37rem;
    line-height: 2.5rem;
    margin-bottom: 2.5rem; }
  .baseline h4 {
    font-size: 1.777rem;
    line-height: 2.5rem;
    margin-bottom: 1.25rem; }
  .baseline h5 {
    font-size: 1.333rem;
    line-height: 1.25rem;
    margin-bottom: 1.25rem; }
  .baseline h6 {
    font-size: 0.75rem;
    line-height: 1.25rem;
    margin-bottom: 1.25rem; }
